html{
  height: 100%;
}
body {  
  margin: 0;
  width: 100%;
  height: 100%;  
  background: #3722aa; /* Old browsers */
  background: -moz-linear-gradient(-45deg,  #3722aa 0%, #010002 23%, #010002 23%, #050203 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg,  #3722aa 0%,#010002 23%,#010002 23%,#050203 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg,  #3722aa 0%,#010002 23%,#010002 23%,#050203 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3722aa', endColorstr='#050203',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}